<script>
    import * as echarts from 'echarts';
    import moment from 'moment'
    import LineChart from '@/components/char'
    import {
        getPlats,
        getPlatInfo,
        getPonitInfo,
        getDime,
    } from '@/api/home'
    export default {
        name: 'platform',
        components: {
            LineChart
        },
        data() {
            return {
                paltName: '', //平台名称
                palt: null, //选中的平台
                paltOptions: [], //所有平台
                defaultShowPlatName: '阳101H91', // 默认显示的平台name
                gk: {
                    today: {},
                    yesterday: {},
                    thismonth: {},
                    all: {},
                },
                tabSelectedJpoint: {}, //计量点tab选中的计量点对象
                pointOptions: [],
                historyCount: {},
                todayCount: [],
                ssjc: {},
                today: '-',
                yesterday: '-',
                tomonth: '-',
                toyear: '-',
                total_end: '-',
                power: 'VObjs',
                powerOptions: [{
                    label: '高压/低压侧相电压',
                    value: 'VObjs',
                }, {
                    label: '高压/低压侧相电流',
                    value: 'IObjs',
                }],
                powerData: {
                    VObjs: [{
                        key: 'uA',
                        maxText: 'U',
                        minText: 'A',
                        unit: ' kV',
                        svgName: require('@/assets/img/5.png'),
                        value: ''
                    }, {
                        key: 'uB',
                        maxText: 'U',
                        minText: 'B',
                        unit: ' kV',
                        svgName: require('@/assets/img/6.png'),
                        value: ''
                    }, {
                        key: 'uC',
                        maxText: 'U',
                        minText: 'C',
                        unit: ' kV',
                        svgName: require('@/assets/img/7.png'),
                        value: ''
                    }], //电压
                    IObjs: [{
                        key: 'lab',
                        maxText: 'I',
                        minText: 'A',
                        unit: 'A',
                        svgName: require('@/assets/img/5.png'),
                        value: ''
                    }, {
                        key: 'lbc',
                        maxText: 'I',
                        minText: 'B',
                        unit: 'A',
                        svgName: require('@/assets/img/6.png'),
                        value: ''
                    }, {
                        key: 'lac',
                        maxText: 'I',
                        minText: 'C',
                        unit: 'A',
                        svgName: require('@/assets/img/7.png'),
                        value: ''
                    }],
                },

                jfDay: 'dimDay',
                dayOptions: [{
                    label: '日',
                    value: 'dimDay',
                }, {
                    label: '月',
                    value: 'dimYwMonth',
                }, {
                    label: '年',
                    value: 'dimYwYear',
                }],
                dimYwYear: '', //当年
                dimYwMonth: '', //当月
                dimDay: '', //当日
                jfObj: {}, //尖峰平谷数据
                pieCharData: {
                    colors: ['#2E4AF7', '#FF8C56', '#EF4474', '#713BDB'],
                    legend: ['尖电量', '峰电量', '平电量', '谷电量'],
                    xAxis: [],
                    series: [],
                    index: null,
                },
                dmdmaxfx: [], //最大需量数据
                lineCharData: {
                    legend: [],
                    xAxis: [],
                    series: [],
                    colors: ['#2E4AF7'],
                    name: ['kw'],
                },
                activeName: '',
            }
        },
        created() {
            getPlats().then(res => {
                this.paltOptions = res.data;
                const defaultPlat = res.data.find(item => item.name == this.defaultShowPlatName)
                this.palt = defaultPlat ? defaultPlat : res.data[0];
                this.paltName = defaultPlat ? defaultPlat.name : res.data[0].name;
                this.getPlatInfo()
            })
            this.today = moment(new Date()).format('YYYY/MM/DD')
            this.yesterday = moment().subtract(1, "days").format("YYYY/MM/DD");
            this.dimYwYear = moment().startOf('year').format('YYYY')
            this.dimYwMonth = moment().startOf('minth').format('YYYY-MM')
            this.dimDay = moment().format('YYYY-MM-DD')
        },
        computed: {
            names: state => state.name
        },
        methods: {
            //处理tab也下换线太长问题
            initDom() {
                if (this.pointOptions.length == 1) { //tab只有一个的时候减少底部线的长度
                    const dom = this.$refs.tabs;
                    const bar = dom.$el.querySelector('.el-tabs__nav-scroll .el-tabs__active-bar');
                    bar.style.width = (parseInt(bar.style.width) - 10) + "px";
                }
            },
            //tab切换函数
            tabClick({
                name,
            }) {
                this.activeName = name.toString();
                this.tabSelectedJpoint = this.pointOptions.find(_ => _.id == name);
                this.getPonitInfo()
            },
            handPalt() {
                this.paltName = this.palt.name;
                this.getPlatInfo()
            },
            getPlatInfo() {
                if (!this.palt) return;
                getPlatInfo({
                    kpCode: this.palt.code
                }).then(res => {
                    this.gk = {
                        ...res.gk
                    }
                    let newOptions = JSON.parse(JSON.stringify(res.jpoints)).map(item => {
                        const name = item.name;
                        if (name.includes('支线')) {
                            item.shortName = name.split('支线')[1]
                        } else if (name.includes('线')) {
                            item.shortName = name.split('线')[1]
                        }
                        return item;
                    })
                    this.activeName = res.jpoints[0].id.toString();
                    if (res.jpoints && res.jpoints.length > 0) {
                        //设置默认平台和计量点
                        this.tabSelectedJpoint = res.jpoints ? res.jpoints[0] : {};
                        this.pointOptions = newOptions
                    } else {
                        this.tabSelectedJpoint = {};
                        this.pointOptions = []
                    }
                    this.getPonitInfo(); //获取计量点信息（即、电量总览，实时监测）
                })
            },
            getPonitInfo() {
                let query = {
                    jpCode: this.tabSelectedJpoint.code ? this.tabSelectedJpoint.code : ''
                }
                getPonitInfo(query).then(res => {
                    this.getDime(); //获取尖峰平谷
                    const {
                        historyCount,
                        ssjc,
                        todayCount,
                        dmdmaxfx
                    } = res
                    this.historyCount = historyCount;
                    this.dmdmaxfx = dmdmaxfx;
                    this.tomonth = moment(historyCount.month_start).format('YYYY/MM/DD') + '-' + moment(
                        historyCount.month_end).format('YYYY/MM/DD');
                    this.toyear = moment(historyCount.total_start).format('YYYY/MM/DD')
                    this.total_end = moment(historyCount.total_end).format('YYYY/MM/DD')

                    this.ssjc = ssjc;
                    this.todayCount = todayCount;
                    this.powerData.VObjs[0].value = ssjc.jlTy == 1 ? ssjc.uA / 1000 : ssjc.uA
                    this.powerData.VObjs[1].value = ssjc.jlTy == 1 ? ssjc.uB / 1000 : ssjc.uB
                    this.powerData.VObjs[2].value = ssjc.jlTy == 1 ? ssjc.uC / 1000 : ssjc.uC
                    this.powerData.VObjs[0].unit = ssjc.jlTy == 1 ? ' kV' : ' V'
                    this.powerData.VObjs[1].unit = ssjc.jlTy == 1 ? ' kV' : ' V'
                    this.powerData.VObjs[2].unit = ssjc.jlTy == 1 ? ' kV' : ' V'

                    this.powerData.IObjs[0].value = ssjc.iA
                    this.powerData.IObjs[1].value = ssjc.iB
                    this.powerData.IObjs[2].value = ssjc.iC
                    this.initLineCharts()
                    // this.initOptions(todayCount)
                }).catch(err => {
                    this.historyCount = {}
                    this.dmdmaxfx = [];
                    this.tomonth = '-';
                    this.toyear = '-';
                    this.total_end = '-'
                    this.todayCount = []
                    this.ssjc = {};
                    this.powerData.VObjs[0].value = ''
                    this.powerData.VObjs[1].value = ''
                    this.powerData.VObjs[2].value = ''
                    this.powerData.IObjs[0].value = ''
                    this.powerData.IObjs[1].value = ''
                    this.powerData.IObjs[2].value = ''
                    this.jfObj = {
                        jQ: 0,
                        fQ: 0,
                        pQ: 0,
                        gQ: 0,
                    }
                    this.initLineCharts()
                    this.initPieChart();
                })
            },
            getDime() { //尖峰平谷
                let query = {
                    [this.jfDay]: this[this.jfDay],
                    dimJpoint: this.tabSelectedJpoint.code ? this.tabSelectedJpoint.code : '',
                }
                getDime(query).then(res => {
                    const arr = res.data && res.data.length > 0 ? res.data : [];
                    const obj = {
                        jQ: arr[0] ? arr[0].jQ : '',
                        fQ: arr[0] ? arr[0].fQ : '',
                        pQ: arr[0] ? arr[0].pQ : '',
                        gQ: arr[0] ? arr[0].gQ : '',
                    }
                    this.jfObj = obj
                    this.initPieChart();
                })
            },
            handPower() {
                if (this.power === 1) this.powerData = this.VObjs
                else if (this.power === 2) this.powerData = this.IObjs
            },
            handJf() {
                this.getDime()
            },
            initLineCharts() {
                this.lineCharData.legend = [];
                this.lineCharData.xAxis = [];
                this.lineCharData.series = [];
                let data = []
                this.dmdmaxfx.map((item, index) => {
                    let st = moment(item.ts).format('YYYY-MM')
                    this.lineCharData.xAxis.push(st)
                    data.push(item.mDmdmax)
                })

                this.lineCharData.grid = {
                    right: 24,
                    bottom: 20,
                    left: 24,
                    top: 68,
                    containLabel: true,
                }

                this.lineCharData.series.push({
                    // symbol: "none",//取消曲线折点上的圆圈
                    name: '最大需量',
                    type: 'line',
                    smooth: true, //设置曲线还是折线
                    areaStyle: {
                        opacity: 0.2,
                        color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                                offset: 0,
                                color: '#d1e9fe'
                            },
                            {
                                offset: 1,
                                color: '#5269f8'
                            }
                        ])
                    },
                    data: data,
                })
            },
            initPieChart() {
                let obj = this.jfObj;
                let data = [{
                    value: obj.jQ ? obj.jQ : 0,
                    name: '尖电量'
                }, {
                    value: obj.fQ ? obj.fQ : 0,
                    name: '峰电量'
                }, {
                    value: obj.pQ ? obj.pQ : 0,
                    name: '平电量'
                }, {
                    value: obj.gQ ? obj.gQ : 0,
                    name: '谷电量'
                }]
                let arr = [];
                data.map(item => {
                    arr.push(item.value)
                })
                var max = Math.max.apply(null, arr);
                this.pieCharData.index = arr.indexOf(max);

                this.pieCharData.series = [{
                    name: '尖峰平谷分析',
                    type: 'pie',
                    center: ['50%', '50%'],
                    avoidLabelOverlap: false,
                    label: {
                        show: false,
                        position: 'center'
                    },
                    emphasis: {
                        label: {
                            show: true,
                            fontSize: '18',
                            fontWeight: 'bold',
                            formatter: '{d} %',
                            color: '自适应'
                        }
                    },
                    radius: ['70%', '90%'],
                    data: data
                }]
            },
        },
        watch: {
            pointOptions: {
                handler(val) {
                    // console.log(val);
                    setTimeout(() => {
                        this.initDom();
                    }, 0);

                },
                immediate: true,
            }
        },
        //keepalive 加载组件事件 再次加载数据
        activated: function () {
            this.getPlatInfo();
        },
        //keepalive 离开组件事件，清空数据
        deactivated: function () {
            this.lineCharData.series = [];
            this.pieCharData.series = [];
        }
    }
</script>
